.site-header__tool-tip {
    position: absolute;
    top: 40px;
    box-shadow: 0px 0px 4.15px 0.85px rgba(0,0,0, 0.65);
    padding: 5px 10px;
    color: #182427;
    font-size: .8125rem;
    background-color: #fff;
    left: 50%;
    transform: translateX(-50%);
    white-space: nowrap;
}

.site-header__tool-tip::after {
  content: "";
  border-color: transparent;
  position: absolute;
  border-width: 9px;
  border-style: solid;
  border-bottom-color: #fff;
  top: -18px;
  left: 50%;
  transform: translateX(-50%);
}

.site-header__tool-tip::before {
  border-color: transparent;
  content: "";
  position: absolute;
  border-width: 10px;
  border-bottom-color: rgba(0,0,0, 0.25);
  border-style: solid;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
}

.site-header__tool-tip--nav, .site-header__tool-tip--fav, .site-header__tool-tip--notify, .site-header__tool-tip--share {
  opacity: 0;
  visibility: hidden;
  transition: all .3s ease-in;
}

.site-header__tool-tip--nav__visible, .site-header__tool-tip--fav__visible, .site-header__tool-tip--notify__visible, .site-header__tool-tip--share__visible {
  opacity: 1;
  visibility: visible;
  transition: all .3s ease-in;
}

