  border-radius: 15px;
  width: 30px;
  height: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
}

.site-header__menu--notify  {
  position: relative;
}

.site-header__menu--notify__cnt {
  position: absolute;
  top: 40px;
  width: 18rem;
  right: -65px;
  background-color: #0284a8;
  padding: 0;
  margin: 0;
  z-index: -2;
  border-radius: 5px;
  cursor: default;
  border-bottom: 5px solid #0284a8;
  border-top: 5px solid #0284a8;
  box-shadow: 0px 1px 4.15px 0.85px rgba(0,0,0, 0.65);
  opacity: 0;
  visibility: hidden;
  transition: all .3s ease-in;
}

.site-header__menu--notify__cnt::before {
  border-color: transparent;
  content: "";
  position: absolute;
  left: 75%;
  transform: translateX(-75%);
  border-bottom-color: #0284a8;
  border-style: solid;
  border-width: 14px;
  top: -28px;
}

@media (min-width: 380px) {

  .site-header__menu--notify__cnt::before {
    left: 66%;
    transform: translateX(-66%);
  }
}

@media (min-width: 530px) {

  .site-header__menu--notify__cnt::before {
    left: 80%;
    transform: translateX(-80%);
  }
}

.site-header__menu--notify__cnt--set {
  position: absolute;
  right: -9px;
  top: -18px;
  height: 24px;
  width: 24px;
  border-radius: 12px;
  background-color: #fff;
  border: 1px solid #0284a8;
  overflow: hidden;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  z-index: 1;
}

.site-header__menu--notify__cnt--det {
  height: 18.75rem;
  overflow: hidden ;
  overflow-x: hidden ;
  overflow-y: auto ;
  background: radial-gradient( circle closest-side, rgb(83,97,125) 0%, rgb(42,129,164) 0%, rgb(19,51,97) 100%);
  background: -o-radial-gradient( circle closest-side, rgb(83,97,125) 0%, rgb(42,129,164) 0%, rgb(19,51,97) 100%);
}

.site-header__menu--notify__cnt--visible {
  opacity: 1;
  visibility: visible;
  z-index: 2;
  transition: all .3s ease-out;
}

@media (min-width: 380px) {

  .site-header__menu--notify__cnt {
    width: 25rem;
